import { Component, Input, OnInit } from '@angular/core';
import { addressToString } from '../../helpers/type-helpers';
import { Address } from '../../models/types';

@Component({
  selector: 'bb-full-address',
  template: `
    <span class="break-word" data-role="full-address-details">
      {{ hostRef.format() }},
      <bb-country-name
        *ngIf="address?.country"
        [countryCode]="address?.country"
      ></bb-country-name>
    </span>
  `,
})
export class FullAddressComponent implements OnInit {
  hostRef = this;

  /**
   * Object containing all the address fields
   */
  @Input() address!: Address;

  ngOnInit() {
    if (!this.address) {
      console.warn('Please provide a valid `address` input');
    }
  }

  format() {
    return addressToString(this.address);
  }
}
